/**
 * Ëé∑ÂèñÊâÄÊúâ‰∫ßÂìÅÂàÜÁ±ª
 * @returns {Array} ÂàÜÁ±ªÂàóË°®
 */
export function getCategories() {
  return categories
}

/**
 * Ëé∑Âèñ‰∫ßÂìÅÂàóË°®
 * @param {string} categoryId - ÂèØÈÄâÁöÑÂàÜÁ±ªID
 * @returns {Array} ‰∫ßÂìÅÂàóË°®
 */
export function getProducts(categoryId) {
  if (!categoryId || categoryId === '') {
    return sampleProducts
  }
  return sampleProducts.filter(product => product.categoryId === categoryId)
}

/**
 * Ëé∑ÂèñÂçï‰∏™‰∫ßÂìÅËØ¶ÊÉÖ
 * @param {string} productId - ‰∫ßÂìÅID
 * @returns {Object|null} ‰∫ßÂìÅÂØπË±°Êàñnull
 */
export function getProductById(productId) {
  if (!productId) {
    return null
  }
  return sampleProducts.find(product => product.id === productId) || null
}

/**
 * Ëé∑ÂèñÂ§ñÈÉ®ÂïÜÂüéÈìæÊé•
 * @param {string} productId - ‰∫ßÂìÅID
 * @returns {string|null} ÂïÜÂüéÈìæÊé•Êàñnull
 */
export function getShopUrl(productId) {
  const product = getProductById(productId)
  if (!product) {
    return null
  }
  return product.shopUrl || null
}

export default {
  categories,
  sampleProducts,
  getCategories,
  getProducts,
  getProductById,
  getShopUrl
}
